/**
 * Internal Text filter component with options support
 */
const TextFilterComponent: React.FC<
  TableFilterProps & {options?: TextFilterOptions}
> = ({column, value, onChange, options}) => {
  const {
    placeholder,
    className,
    style,
    defaultValue,
    delay = 500,
    getFilter,
    onFilter,
    id,
    disabled,
  } = options || {};

  // Local state for immediate input updates (maintains focus)
  const [internalValue, setInternalValue] = useState(
    value || defaultValue || ''
  );
  const internalValueRef = useRef(internalValue);
  const inputRef = useRef<HTMLInputElement>(null);
  const onChangeRef = useRef(onChange);
  const onFilterRef = useRef(onFilter);

  // Keep refs in sync
  useEffect(() => {
    internalValueRef.current = internalValue;
  }, [internalValue]);

  useEffect(() => {
    onChangeRef.current = onChange;
    onFilterRef.current = onFilter;
  }, [onChange, onFilter]);

  // Debounce the internal value
  const [debouncedValue] = useDebouncedValue(internalValue, {wait: delay});

  // Propagate debounced value to parent
  useEffect(() => {
    onChangeRef.current(debouncedValue || null);
    onFilterRef.current?.(debouncedValue);
  }, [debouncedValue]);

  // Provide filter instance via getFilter callback - only on mount
  useEffect(() => {
    if (getFilter) {
      getFilter({
        get value() {
          return internalValueRef.current;
        },
        setValue: (newValue: string) => {
          setInternalValue(newValue);
          onChangeRef.current(newValue || null);
          onFilterRef.current?.(newValue);
        },
        clear: () => {
          setInternalValue('');
          onChangeRef.current(null);
          onFilterRef.current?.('');
        },
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [getFilter]);

  // Simple change handler - just update local state
  const handleChange = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    setInternalValue(e.target.value);
  }, []);

  const inputStyle: React.CSSProperties = {
    fontWeight: 400,
    ...style,
  };

  // If custom className is provided, use plain input to allow full CSS control
  if (className) {
    return (
      <input
        ref={inputRef}
        type="text"
        id={id}
        value={internalValue}
        onChange={handleChange}
        placeholder={
          placeholder || column.filterPlaceholder || `Filter ${column.text}...`
        }
        className={className}
        style={{
          width: '100%',
          fontWeight: 400,
          ...style,
        }}
        disabled={disabled}
      />
    );
  }

  return (
    <FilterInputBase
      ref={inputRef}
      type="text"
      id={id}
      value={internalValue}
      onChange={handleChange}
      placeholder={
        placeholder || column.filterPlaceholder || `Filter ${column.text}...`
      }
      style={inputStyle}
      disabled={disabled}
    />
  );
};

/**
 * Text filter - can be used as a component or factory function
 *
 * @example
 * // As a factory function with options
 * filter: TextFilter({
 *   placeholder: 'Search...',
 *   className: 'my-custom-class',
 *   getFilter: (filter) => { myFilterRef = filter; },
 * })
 *
 * @example
 * // As a component (for direct usage)
 * <TextFilter column={column} value={value} onChange={onChange} />
 */
export function TextFilter(
  options: TextFilterOptions
): React.FC<TableFilterProps>;
export function TextFilter(props: TableFilterProps): React.ReactElement;
export function TextFilter(
  optionsOrProps: TextFilterOptions | TableFilterProps
): React.FC<TableFilterProps> | React.ReactElement {
  // Check if it's being used as a factory function (options object without column/value/onChange)
  if (
    !('column' in optionsOrProps) &&
    !('value' in optionsOrProps) &&
    !('onChange' in optionsOrProps)
  ) {
    // Factory function usage - return a component
    const options = optionsOrProps as TextFilterOptions;
    const FilterWithOptions: React.FC<TableFilterProps> = (props) => (
      <TextFilterComponent {...props} options={options} />
    );
    FilterWithOptions.displayName = 'TextFilter';
    return FilterWithOptions;
  }

  // Direct component usage
  const props = optionsOrProps as TableFilterProps;
  return <TextFilterComponent {...props} />;
}

/**
 * Internal Number filter component with options support
 */
const NumberFilterComponent: React.FC<
  TableFilterProps & {options?: NumberFilterOptions}
> = ({column, value, onChange, options}) => {
  const {
    placeholder,
    className,
    style,
    defaultValue,
    delay = 500,
    defaultComparator = '=',
    allowDecimal = true,
    getFilter,
    onFilter,
    id,
    disabled,
    hideComparator,
    comparators = ['=', '!=', '>', '>=', '<', '<='],
  } = options || {};

  // Local state for immediate input updates (maintains focus)
  const [number, setNumber] = useState(
    value?.number || defaultValue?.number || ''
  );
  const [comparator, setComparator] = useState(
    value?.comparator || defaultValue?.comparator || defaultComparator
  );
  const numberRef = useRef(number);
  const comparatorRef = useRef(comparator);
  const inputRef = useRef<HTMLInputElement>(null);
  const onChangeRef = useRef(onChange);
  const onFilterRef = useRef(onFilter);

  // Keep refs in sync with state
  useEffect(() => {
    numberRef.current = number;
    comparatorRef.current = comparator;
  }, [number, comparator]);

  useEffect(() => {
    onChangeRef.current = onChange;
    onFilterRef.current = onFilter;
  }, [onChange, onFilter]);

  // Debounce the number value
  const [debouncedNumber] = useDebouncedValue(number, {wait: delay});

  // Propagate debounced value to parent
  useEffect(() => {
    const newValue = debouncedNumber
      ? {number: debouncedNumber, comparator: comparatorRef.current}
      : null;
    onChangeRef.current(newValue);
    onFilterRef.current?.(newValue);
  }, [debouncedNumber]);

  // Provide filter instance via getFilter callback - only on mount
  useEffect(() => {
    if (getFilter) {
      getFilter({
        get value() {
          return numberRef.current
            ? {number: numberRef.current, comparator: comparatorRef.current}
            : null;
        },
        setValue: (newValue) => {
          if (newValue) {
            setNumber(newValue.number);
            setComparator(newValue.comparator);
            onChangeRef.current(newValue);
            onFilterRef.current?.(newValue);
          } else {
            setNumber('');
            onChangeRef.current(null);
            onFilterRef.current?.(null);
          }
        },
        clear: () => {
          setNumber('');
          setComparator(defaultComparator);
          onChangeRef.current(null);
          onFilterRef.current?.(null);
        },
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [getFilter]);

  // Handle comparator change - trigger immediate filter update
  const handleComparatorChange = useCallback(
    (newComparator: string) => {
      setComparator(newComparator);
      if (number) {
        const newValue = {number, comparator: newComparator};
        onChangeRef.current(newValue);
        onFilterRef.current?.(newValue);
      }
    },
    [number]
  );

  const comparatorSymbols: Record<string, string> = {
    '=': '=',
    '!=': '≠',
    '>': '>',
    '>=': '≥',
    '<': '<',
    '<=': '≤',
  };

  const inputStyle: React.CSSProperties = {
    flex: 1,
    fontWeight: 400,
    ...style,
  };

  const inputProps = {
    type: 'text' as const,
    id,
    value: number,
    onChange: (e: React.ChangeEvent<HTMLInputElement>) => {
      const val = e.target.value;
      const pattern = allowDecimal ? /^[0-9.,]*$/ : /^[0-9]*$/;
      if (pattern.test(val)) {
        setNumber(val);
      }
    },
    placeholder: placeholder || column.filterPlaceholder || 'Number...',
    disabled,
  };

  return (
    <FilterContainer>
      {!hideComparator && (
        <ComparatorSelect
          value={comparator}
          onChange={(e) => handleComparatorChange(e.target.value)}
          disabled={disabled}
        >
          {comparators.map((comp) => (
            <option key={comp} value={comp}>
              {comparatorSymbols[comp] || comp}
            </option>
          ))}
        </ComparatorSelect>
      )}
      {className ? (
        <input
          ref={inputRef}
          {...inputProps}
          className={className}
          style={{flex: 1, width: '100%', fontWeight: 400, ...style}}
        />
      ) : (
        <FilterInputBase ref={inputRef} {...inputProps} style={inputStyle} />
      )}
    </FilterContainer>
  );
};

